* {
  box-sizing: border-box;
}

body {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #212121;
  background-color: #fafafa;
  font-family: -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Oxygen-Sans, Ubuntu, Cantarell, Helvetica Neue, sans-serif;
  line-height: 1.5;
}

img {
  max-width: 100%;
  display: block;
}

button[data-start], button[data-stop] {
  height: 40px;
  width: 80px;
  color: #000;
  background-color: #e6e6fa;
  border-radius: 12px;
  margin-right: 20px;
}

.timer {
  margin-top: 10px;
  display: flex;
}

.field {
  width: 60px;
  flex-direction: column;
  margin-left: 10px;
  margin-right: 10px;
  display: flex;
}

button {
  cursor: pointer;
  color: #000;
  border: none;
  font-size: 14px;
  display: inline-block;
}

.value {
  text-align: center;
  letter-spacing: 4px;
  color: #000;
  background-color: pink;
  font-size: 40px;
  font-weight: 500;
}

.label {
  text-align: center;
  text-transform: uppercase;
  color: #000;
  font-size: 15px;
  font-weight: 400;
}

